/*Setting organic knowledge videos*/

.organic-title{
    font-family: 'Titillium Web', sans-serif;
    font-size: 36px;
    padding-left: 10px;
    padding-bottom: 50px;
    color: #000;
}

.container-videos-organic{
    width: 90%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.container-videos-organic iframe{
    width: 560px;
    height: 315px;
    margin-bottom: 30px;
}

/*-------------------------Media Query max-width: 550px-------------------------*/

@media (max-width: 550px){

/*Setting organic knowledge videos*/

.container-videos-organic iframe{
    width: 300px;
    height: 200px;
    margin-bottom: 30px;
}

}