/*Google fonts:*/

@import url('https://fonts.googleapis.com/css2?family=Titillium+Web:wght@300;400;600&display=swap');

/*Setting products menu's buttons*/

.container-products-menu{
    width: 90%;
    height: 310px;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    border-radius: 5px;
}

.title-part{
    font-family: 'Titillium Web', sans-serif;
    font-size: 36px;
    padding-left: 10px;
    padding-bottom: 50px;
    color: #fff;
}

.garden-icon{
    width: 200px;
    height: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 100%;
    background-color: #fff;
    cursor: pointer;
}

.flower{
    color: #5ab507;
    font-size: 100px;
}

.farming-icon{
    width: 200px;
    height: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 100%;
    background-color: #fff;
    cursor: pointer;
}

.plant{
    color: #5ab507;
    font-size: 100px;
}

.garden-icon:hover{
    width: 210px;
    height: 210px;
}

.farming-icon:hover{
    width: 210px;
    height: 210px;
}

.container-garden{
    padding: 20px 20px;
    top: 5px;
    left: 100px;
}

.garden-title{
    font-family: 'Titillium Web', sans-serif;
    font-size: 25px;
    text-decoration: none;
    color: #fff;
}

.container-farming{
    padding: 20px 20px;
    top: 5px;
    right: 100px;
}

.farming-title{
    font-family: 'Titillium Web', sans-serif;
    font-size: 25px;
    text-decoration: none;
    color: #fff;
}

.garden-title, .farming-title{
    text-align: center;
    margin-bottom: 20px;
}

/*-------------------------Media Query max-width: 550px-------------------------*/

@media (max-width: 550px){

/*Setting products menu's buttons*/

.container-products-menu{
    height: 620px;
    flex-direction: column;
    align-items: center;
}

}

/*-------------------------Media Query min-width: 1280px-------------------------*/

@media (min-width: 1280px){

/*Setting products menu's buttons*/

.garden-title{
    font-size: 30px;
}

.farming-title{
    font-size: 30px;
}
        
}