/*Google fonts:*/

@import url('https://fonts.googleapis.com/css2?family=Titillium+Web:wght@300;400;600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@700&display=swap');

/*Setting farming products*/

.container-farming-products{
    width: 90%;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 50px;
}

.title-farming-catalogue{
    font-family: 'Titillium Web', sans-serif;
    font-size: 36px;
    padding-left: 10px;
    padding-bottom: 50px;
    color: #000;
}

.product-farming-item{
    width: 15%;
    height: 275px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 15px 15px;
    border: 2px solid rgb(230, 230, 230);
    cursor: pointer;
}

.product-farming-item:hover{
    border: none;
    box-shadow: 2px 2px 2px 1px gray;
}

.name-product{
    font-family: 'Titillium Web', sans-serif;
    font-size: 18px;
    color: #000;
}

.bacter-sp {
    padding-bottom: 10px;
    width: 185px;
}

.bacter-sm {
    padding-top: 10px;
    padding-bottom: 10px;
    width: 160px;
}

.polisulfuro {
    padding-top: 10px;
    padding-bottom: 10px;
    width: 160px;
}

.abono {
    width: 150px;
}

/*Setting garden products*/

.container-garden-products{
    width: 90%;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 50px;
}

.title-garden-catalogue{
    font-family: 'Titillium Web', sans-serif;
    font-size: 36px;
    padding-left: 10px;
    padding-bottom: 50px;
    color: #000;
}

.product-garden-item{
    width: 15%;
    height: 275px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 15px 15px;
    border: 2px solid rgb(230, 230, 230);
    cursor: pointer;
}

.product-garden-item:hover{
    border: none;
    box-shadow: 2px 2px 2px 1px gray;
}

.bacter-sp-gr{
    width: 95px;
}

.bacter-sm-gr{
    width: 110px;
}

.carbon-activado-gr{
    padding-top: 20px;
    padding-bottom: 23px;
    width: 175px;
}

.fungicida-gr{
    padding-top: 12px;
    width: 175px;
}

.enraizador-gr{
    padding-top: 5px;
    padding-bottom: 5px;
    width: 110px;
}

.huerto-gr{
    padding-top: 50px;
    padding-bottom: 55px;
    width: 190px;
}

.compostera-g{
    padding-bottom: 40px;
    width: 200px;
}

.abono-gr {
    padding-bottom: 5px;
    width: 150px;
}

/*-------------------------Media Query max-width: 550px-------------------------*/

@media (max-width: 550px){

/*Setting farming products*/

.container-farming-products{
    flex-direction: column;
    align-items: center;
}

.product-farming-item{
    width: 80%;
    margin-bottom: 20px;
}

/*Setting garden products*/

.container-garden-products{
    flex-direction: column;
    align-items: center;
    margin-bottom: 0px;
}

.product-garden-item{
    width: 80%;
    margin-bottom: 20px;
}

}

