/*Google fonts:*/

@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');

/*Setting our custumer section*/

.customer-title{
    font-family: 'Titillium Web', sans-serif;
    font-size: 36px;
    padding-left: 10px;
    padding-bottom: 50px;
    color: #000;
}

.container-customer-img{
    width: 90%;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.orquideas-img{
    width: 25%;
}

.botanika{
    font-family: 'Bebas Neue', cursive;
    font-size: 50px;
}

/*-------------------------Media Query max-width: 550px-------------------------*/

@media (max-width: 550px){

/*Setting our custumer section*/

.container-customer-img{
    flex-direction: column;
}

.orquideas-img{
    width: 80%;
}

.caoba-img, .botanika, .orquideas-img{
    margin-bottom: 50px;
}

}

/*-------------------------Media Query min-width: 550px-------------------------*/

@media (min-width: 550px){

/*Setting our custumer section*/

.caoba-img, .botanika, .orquideas-img{
    margin-bottom: 50px;
}

.container-customer-img{
    flex-direction: column;
}

}