/*Setting images of the slider*/

.container-image{
    width: 100%;
    display: flex;
    flex-direction: row;
    animation: slider 20s infinite alternate linear;
}

#about-us img{
    width: 100%;
}

@keyframes slider{

    0% {margin-left: 0;}
    20% {margin-left: 0;}

    25% {margin-left: -100%;}
    45% {margin-left: -100%;}

    50% {margin-left: -200%;}
    70% {margin-left: -200%;}

    75% {margin-left: -300%;}
    100% {margin-left: -300%;}
}

/*-------------------------Media Query max-width: 550px-------------------------*/

@media (max-width: 550px){

/*Setting images of the slider*/

.container-image{
    width: 100%;
    display: flex;
    flex-direction: row;
    animation: slider 20s infinite alternate linear;
}

}