/*Google fonts:*/

@import url('https://fonts.googleapis.com/css2?family=Titillium+Web:wght@300;400;600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@700&display=swap');


/*Setting footer*/

.principal-container-contact{
    width: 90%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding-top: 50px;
    padding-bottom: 30px;
    margin: 0 auto;
}

.special-style{
    margin-bottom: 20px;
    font-family: 'Titillium Web', sans-serif;
    font-size: 20px;
    font-weight: bold;
}

.info-item{
    font-family: 'Titillium Web', sans-serif;
    font-size: 18px;
}

.container-net{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.net-item{
    font-size: 40px;
}

.net-item a{
    text-decoration: none;
    color: #000
}

/*-------------------------Media Query max-width: 550px-------------------------*/

@media (max-width: 550px){

/*Setting footer*/

.principal-container-contact{
    flex-direction: column;
}

.container-info{
    margin-bottom: 20px;
}

.container-net{
    justify-content: space-around;
}

}